@media (min-width: 1024px) {
  .container {
    min-width: 1024px; } }

@media (min-width: 768px) and (max-width: 1023px) {
  .container {
    min-width: 768px;
    max-width: 1023px; } }

@media (min-width: 320px) and (max-width: 767px) {
  .container {
    min-width: 320px;
    max-width: 767px;
    margin: 0 auto; } }

.header {
  color: #ffffff;
  background-color: #1b2d37;
  background-image: url("../img/bg.png");
  background-size: 100%; }

.header__top {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 25px; }

.header__contacts {
  display: flex;
  justify-content: space-between;
  align-items: center; }

.title {
  display: flex;
  justify-content: space-between;
  align-items: center; }

.callback__element {
  padding: 10px;
  margin-right: 50px;
  font-size: 16px;
  font-family: "PT Mono"; }

.callback__element--link {
  color: #ffffff;
  text-decoration: none; }

.callback__element--call {
  padding: 14px 22px;
  margin-right: 0;
  font-weight: bold;
  text-transform: uppercase;
  font-family: "Roboto Condensed";
  border: 2px solid #ffffff;
  border-radius: 5px; }

@media (min-width: 320px) and (max-width: 1023px) {
  .callback__element {
    display: none; }
  .callback__pic--email {
    width: 25px;
    height: 18px;
    margin-right: 33px; }
  .callback__pic--phone {
    width: 22px;
    height: 22px; } }

.name {
  margin-left: 16px;
  text-align: left; }

.name__bold {
  margin-bottom: 5px;
  margin-top: 5px;
  font-size: 25px;
  font-weight: bold; }

.name__bold--footer {
  color: #203c42; }

.name__grey {
  margin: 0;
  color: #788891;
  font-size: 12px; }

.promo {
  display: flex;
  justify-content: space-between;
  align-items: center;
  overflow: hidden; }

.promo__img {
  padding: 100px 30px 70px 30px; }

.promo-text {
  padding-left: 80px;
  color: #ffffff; }

.promo-text__title {
  margin: 0;
  margin-bottom: 30px;
  font-weight: bold;
  font-size: 53px;
  text-transform: uppercase; }

.promo-text__desc {
  position: relative;
  margin: 0;
  padding-left: 50px;
  min-width: 438px;
  font-size: 20px;
  font-family: "PT Mono";
  text-align: left; }

.promo-text__desc::before {
  position: absolute;
  content: "";
  left: 0px;
  top: 10px;
  width: 45px;
  height: 2px;
  background-image: url("../img/line.svg"); }

.promo-text__link {
  display: inline-block;
  padding: 16px 24px;
  margin-top: 37px;
  margin-left: 50px;
  text-transform: uppercase;
  font-size: 16px;
  background-color: #00b2ff;
  border-radius: 5px; }

.footer {
  padding-top: 25px;
  color: #1b2d37;
  background-color: #fafafa; }

.footer__title {
  margin-bottom: 0;
  text-align: left;
  font-size: 15px;
  color: #1b2d37;
  text-transform: uppercase;
  font-weight: normal; }

.footer__top {
  display: flex;
  justify-content: space-around;
  align-items: center; }

.footer__bottom {
  display: flex;
  justify-content: space-around;
  align-items: center;
  text-transform: uppercase;
  font-size: 13px;
  border-top: 1px solid #626962; }

@media (min-width: 320px) and (max-width: 1023px) {
  .footer__top {
    display: block;
    margin-left: auto;
    margin-right: auto; } }

@media (min-width: 320px) and (max-width: 767px) {
  .footer__bottom {
    display: block; } }

.name-social__item {
  display: flex; }

.name-social__element--service {
  display: none; }

@media (min-width: 768px) and (max-width: 1023px) {
  .name-social {
    display: flex;
    justify-content: space-around;
    padding-bottom: 27px; } }

@media (min-width: 320px) and (max-width: 1023px) {
  .name-social__element--service {
    display: block;
    margin-top: 27px;
    color: #1b2d37;
    opacity: 0.5;
    font-size: 13px;
    text-transform: uppercase; } }

@media (min-width: 320px) and (max-width: 767px) {
  .name-social__item {
    margin-left: 25%;
    margin-right: 25%; } }

.social {
  display: flex;
  margin-top: 54px; }

.social__element {
  margin-right: 15px; }

.social__element:last-child {
  margin-right: 0; }

@media (min-width: 768px) and (max-width: 1023px) {
  .social {
    align-items: center;
    margin-top: 0; } }

@media (min-width: 320px) and (max-width: 767px) {
  .social {
    margin-top: 32px;
    margin-left: 35%;
    margin-right: 35%; } }

.description {
  display: flex;
  justify-content: space-around;
  text-align: left;
  opacity: 0.5;
  font-family: "PT Mono", sans-serif; }

.description__list {
  padding: 0;
  margin-right: 140px;
  list-style-type: none; }

.description__list--first {
  margin-right: 40px; }

@media (min-width: 320px) and (max-width: 1023px) {
  .description {
    display: block; }
  .description__list {
    margin: 0; }
  .description__list--first {
    margin-top: 29px; } }

.contents-contacts {
  display: flex;
  justify-content: space-around;
  align-items: flex-start;
  margin-bottom: 33px;
  padding-top: 38px; }

@media (min-width: 768px) and (max-width: 1023px) {
  .contents-contacts {
    border-top: 1px solid #626962; } }

@media (min-width: 320px) and (max-width: 767px) {
  .contents-contacts {
    display: block; } }

.contacts {
  text-align: left; }

.contacts__list {
  padding: 0;
  list-style-type: none; }

@media (min-width: 320px) and (max-width: 767px) {
  .contacts {
    padding-top: 28px;
    padding-left: 15px;
    border-top: 1px solid #626962; } }

@media (min-width: 768px) and (max-width: 1023px) {
  .contents {
    display: block;
    margin-left: 25px; } }

@media (min-width: 320px) and (max-width: 767px) {
  .contents {
    padding-top: 28px;
    padding-bottom: 28px;
    padding-left: 15px;
    border-top: 1px solid #626962; } }

.contacts-list {
  opacity: 0.5;
  font-family: "PT Mono", sans-serif; }

.contacts-list__link {
  color: #1b2d37;
  text-decoration: none; }

@media (min-width: 768px) and (max-width: 1023px) {
  .contacts-list {
    margin-top: 29px; } }

.rights {
  padding-top: 21px;
  padding-bottom: 14px;
  opacity: 0.5;
  font-family: "PT Mono", sans-serif; }

.rights__link {
  color: #1b2d37;
  text-decoration: none; }

.rights__credits {
  display: flex; }

.credits__element--copyright {
  margin-right: 59px; }

@media (min-width: 320px) and (max-width: 1023px) {
  .credits {
    display: none; } }

body {
  margin: 0;
  padding: 0;
  font-family: "Roboto", "Helvetica", sans-serif;
  font-size: 14px;
  letter-spacing: 0;
  line-height: 21px;
  box-sizing: border-box; }
